/* // @import '../../themes/theme2/color.less'; */

.dms-layout .ant-tabs > .ant-tabs-nav {
  position: relative;
  display: flex;
  padding-top: 10px;
  padding-left: 5px;
  margin: 0px 16px;
  flex: none;
  align-items: center;
}
.dms-layout .ant-tabs-top > .ant-tabs-nav {
  margin: 0px 4px;
}
.template-dms-badge.template-avatar.ant-badge {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: rgba(0, 0, 0, 0.85);
  font-size: 14px;
  font-variant: tabular-nums;
  line-height: 1.5715;
  list-style: none;
  font-feature-settings: 'tnum', 'tnum';
  position: relative;
  display: flex;
  line-height: 1;
}
.dms-tabs .ant-tabs-nav-wrap > .ant-tabs-nav-list > .ant-tabs-tab {
  border-radius: 3px 3px 0 0;
  padding: 0;
}
.dms-tabs .ant-tabs-nav-list {
  padding: 0;
}

.dms-tabs .ant-tabs-tab + .ant-tabs-tab {
  margin: 0;
}
.dms-layout .template-submenu.ant-menu-submenu > .ant-menu-submenu-title {
  height: 100%;
  margin: 0px;
}
