/* // @import '../../themes/theme2/color.less'; */
:root {
  /* @primary-color: #1890ff; */
  --link-color: #fff;
  /* --slider: #ffffff; */
  --header: #4f448d;
  --header-text-color: #fff;
  --layout: #f4f4f4;
  --color-bck: #fafafa;
  --menu-dark: #00152900;
  --submenu-dark: #3b3eb2eb;
  --menu-selected: #4f448dd9;
  --menu-active: #ffffffd9;
  --menu-text: #261c2a;
  --btn-primary: #4f448dd9;
  --border-btn-primary: #4f448dd9;
  --btn-primary-focus: #6256a9bf;
  --text-btn-primary-focus: #ffffff;
  --border-btn-primary-focus: #6256a9bf;
  --tabs-active-text-color: #474555;
  --tab-active-bck-color: #ffffff;
  --tabs-active-text-color-hover: #37344d;
  --tab-active-bck-color-hover: #ffffffa3;
  --tab-active-shadow-color: 0 0 5px 0px #00000030;
  --tab-active-ink-bar: #ffffff00;
  --tabs-notselected-bck-color: #7f7c8b41;
  --tabs-notselected-text-color: #1b1445;
  --tabs-notselected-bck-color-hover: #4f448d8c;
  --tabs-notselected-text-color-hover: #ffffff;
  --tab-content-bck-color: #f8f8f800;
  --tab-content-bck-color-secandary: #ffffff;
  --tab-header-bck-color: #ffffff;
  --tab-header-border-color: 1px solid #b4b1cf59;
  --tab-header-text-color: rgb(255, 0, 0);
  --tab-header-text-secandary: rgb(123 123 123);
  --tab-header-text-primary: rgb(29, 27, 27);
  --table-head-text-color: rgb(1 5 40 / 75%);
  --table-head-bck-color: linear-gradient(180deg, #ffffff, #5e4ad833);
  --table-head-bck-color-open: linear-gradient(180deg, #ffffff, #5e4ad87a);
  --table-head-bck-color-hover: linear-gradient(180deg, #ffffff, #5e4ad880);
  --table-head-border-color: 1px solid #dcd8f3;
  --table-icon-primary: #8176c0;
  --table-icon-secandary: #3f3d51;
  --table-icon-head: #3f3d51;
  --tab-active-bck-color-secandary: #ffffff;
  --tabs-active-text-color-hover-secandary: #ffffff;
  --tab-active-bck-color-hover-secandary: #ffffff;
  --tab-active-shadow-color-secandary: 0 0 5px 0px #00000030;
  --tab-active-ink-bar-secandary: #ffffff00;
  --tabs-notselected-bck-color-hover-secandary: #4f448d8c;
  --tabs-notselected-text-color-hover-secandary: #ffffff;
  --border-main-content: #7c789d;
  --border-main-content-2: #9498a1;
  --border-main-content-bck-color: #ffffff;
  --border-main-content-text: #2b3245;
}
/*: var(); */
.default-theme
  .dms-layout
  .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover {
  border-color: var(--header);
}

.default-theme .dms-layout .ant-input-affix-wrapper-focused {
  border-color: var(--header);
  box-shadow: 0 0 0 2px #4f448d17;
}
.default-theme .ant-input:hover {
  border-color: var(--header);
}

.default-theme .ant-input:focus {
  border-color: var(--header);
  box-shadow: 0 0 0 2px #4f448d1f;
}
.default-theme .dms-layout .layout-principale.bck-image {
  /* color: var(--border-main-content-text); */
  background-color: var(--color-bck);
  background-image: var(--image-bck);

  background-size: cover;
  background-position: center;
}
.default-theme .dms-layout .border-main-content {
  color: var(--border-main-content-text);
  border-top-color: var(--border-main-conten);
}
.default-theme .dms-layout .border-main-content-2 {
  border-color: var(--border-main-content-2);
  background-color: var(--border-main-content-bck-color);
}
.default-theme .dms-layout .content-tab {
  background-color: var(--tab-content-bck-color);
}
.default-theme .dms-layout .header-tab {
  border-bottom: var(--tab-header-border-color);
  background: var(--tab-header-bck-color);
  color: var(--tab-header-text-color);
}

.default-theme .dms-layout .ant-layout-sider {
  background: var(--slider);
}
.default-theme .dms-layout .ant-layout-header {
  background: var(--header);
}
.default-theme .dms-layout .header-icon-color {
  color: var(--header-text-color);
}

.default-theme .dms-layout .template-menu-principale {
  background: var(--menu-dark);
  color: var(--menu-text);
  border-left: 0px solid #fff;
}
.default-theme .dms-layout .template-menu-principale .ant-menu-sub {
  background: rgba(255, 255, 255, 0);
  color: var(--menu-text);
  border: 0px solid rgba(255, 255, 255, 0);
}
.default-theme .dms-layout .template-menu-principale .ant-menu-item-selected {
  background: rgba(103, 75, 148, 0.144);
  /* var(--menu-selected); */
  color: var(--menu-selected);
  border-left: 3px solid var(--menu-selected);
}
.default-theme .dms-layout .template-menu-principale .ant-menu-item-active {
  color: var(--menu-selected);
  background: rgba(200, 188, 219, 0.233);
  /* border: 0px solid rgba(255, 255, 255, 0); */
}
.default-theme
  .dms-layout
  .template-menu-principale
  .ant-menu-submenu-selected {
  background: rgba(103, 75, 148, 0.144);
  /* var(--menu-selected); */
  color: var(--menu-selected);
  border-left: 3px solid var(--menu-selected);
  /* border: 8px solid rgba(255, 0, 0, 0.979); */
}
.default-theme
  .dms-layout
  .template-menu-principale
  .ant-menu-submenu-active
  .ant-menu-submenu-title:hover {
  color: var(--menu-selected);
  background: rgba(200, 188, 219, 0.233);
}
.default-theme
  .dms-layout
  .template-menu-principale.ant-menu-inline
  .ant-menu-item::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  /* border-right: 3px solid #1890ff; */
  transform: scaleY(0.0001);
  opacity: 0;
  transition: transform 0.15s cubic-bezier(0.215, 0.61, 0.355, 1),
    opacity 0.15s cubic-bezier(0.215, 0.61, 0.355, 1);
  content: "";
}
.default-theme .dms-layout .ant-layout {
  background: var(--layout);
}
.default-theme .dms-layout .ant-btn-primary {
  background: var(--btn-primary);
  border-color: var(--border-btn-primary);
}

.default-theme
  .dms-layout
  .ant-tabs-card.ant-tabs-top
  > .ant-tabs-nav
  .ant-tabs-tab-active {
  border-bottom-color: #fff;
}
.default-theme .dms-layout .ant-tabs-card > .ant-tabs-nav .ant-tabs-tab-active {
  color: #f8f8f8d9;
  background: #032031d0;
}

.default-theme .dms-tabs .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
  color: var(--tabs-active-text-color);
  /* font-weight: 500; */
  /* text-shadow: 0 0 0.25px currentColor; */
}
.default-theme .dms-tabs .ant-tabs-tab {
  position: relative;
  display: inline-flex;
  align-items: center;
  padding: 12px 0;
  font-size: 14px;
  background: var(--tabs-notselected-bck-color);
  border: 0;
  color: var(--tabs-notselected-text-color);
  outline: none;
  cursor: pointer;
}
.default-theme .dms-tabs .ant-tabs-tab-active {
  box-shadow: var(--tab-active-shadow-color);
  background-color: var(--tab-active-bck-color);
}
.default-theme .dms-tabs .ant-tabs-ink-bar {
  position: absolute;
  background: var(--tab-active-ink-bar);
  pointer-events: none;
}
.default-theme .dms-tabs .ant-tabs-tab:hover {
  color: var(--tabs-notselected-text-color-hover);
  background-color: var(--tabs-notselected-bck-color-hover);
}
.default-theme .dms-tabs .ant-tabs-tab.ant-tabs-tab-active:hover {
  color: var(--tabs-active-text-color-hover);
  /* font-weight: 500; */
  background-color: var(--tab-active-bck-color-hover);
  /* text-shadow: 0 0 0.25px currentColor; */
}
.default-theme .dms-layout .ant-menu-dark .ant-menu-inline.ant-menu-sub {
  background: var(--submenu-dark);
}
.default-theme .dms-layout .ant-menu-sub,
.ant-menu.ant-menu-dark .ant-menu-sub {
  background: #001529eb;
}
.default-theme .dms-layout .ant-btn-primary:focus {
  background: #001529eb;
  color: var(--text-btn-primary-focus);
  background: var(--btn-primary-focus);
  border-color: var(--border-btn-primary-focus);
}
.default-theme .ant-btn:focus {
  color: var(--text-btn-primary-focus);
  border-color: var(--border-btn-primary-focus);
}
.default-theme .ant-btn:hover,
.ant-btn:focus {
  color: var(--text-btn-primary-focus);
  border-color: var(--border-btn-primary-focus);
}
/*  */
.default-theme .template-dms .ant-table-thead > tr > th {
  color: var(--table-head-text-color);
  font-weight: 500;
  text-align: left;
  /* background-image: var(--table-head-bck-color);
  border-bottom: var(--table-head-border-color); */
  transition: background 0.3s ease;
}
/* .default-theme
  .template-dms
  .ant-table-thead
  th.ant-table-column-has-sorters:hover { 
  background-image: var(--table-head-bck-color-hover);
} */
.default-theme .template-dms .dms-table-icon-secandary {
  color: var(--table-icon-secandary);
}
.default-theme .template-dms .dms-table-icon-primary {
  color: var(--table-icon-primary);
}
.default-theme .ant-table-filter-trigger {
  display: block;
  width: 2.3em;
  color: var(--table-icon-head);
  font-size: 12px;
  transition: color 0.3s;
}
/* .default-theme .template-dms .ant-table-filter-trigger-container-open {
  background-image: var(--table-head-bck-color-open);
}
.default-theme .template-dms .ant-table-filter-trigger-container:hover {
  background-image: var(--table-head-bck-color-hover);
} */
.default-theme .template-dms .ant-table-column-sorter {
  margin-top: 0.15em;
  margin-bottom: -0.15em;
  margin-left: 8px;
  color: var(--table-icon-head);
}
.default-theme .tabs-formulaire.dms-tabs .ant-tabs-tab {
  position: relative;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
  align-items: center;
  padding: 1px 0;
  font-size: 14px;
  background: rgb(0 0 0 / 8%);
  /* border: 2px solid #fff; */
  color: var(--tabs-notselected-text-color-secandary);
  outline: none;
  cursor: pointer;
}
.default-theme .tabs-formulaire.dms-tabs .ant-tabs-tab-active {
  box-shadow: var(--tab-active-shadow-color-secandary);
  background-color: var(--tab-active-bck-color-secandary);
}
.default-theme .tabs-formulaire.dms-tabs .ant-tabs-ink-bar {
  position: absolute;
  background: var(--tab-active-ink-bar-secandary);
  pointer-events: none;
}
.default-theme .tabs-formulaire.dms-tabs .ant-tabs-tab:hover {
  color: var(--tabs-notselected-text-color-hover-secandary);
  background-color: var(--tabs-notselected-bck-color-hover-secandary);
}
.default-theme
  .tabs-formulaire.dms-tabs
  .ant-tabs-tab.ant-tabs-tab-active:hover {
  color: var(--tabs-active-text-color-hover-secandary);
  background-color: var(--tab-active-bck-color-hover-secandary);
}
.default-theme .tabs-formulaire.dms-tabs .template-tabs-panel {
  background-color: var(--tab-content-bck-color-secandary);
  padding: 4px;
  border-radius: 3px;
  /* height: 500px; */
}
.default-theme .header-tabs-text-secandary {
  color: var(--tab-header-text-secandary);
  /* height: 500px; */
}
.default-theme .header-tabs-text-primary {
  color: var(--tab-header-text-primary);
  /* height: 500px; */
}
